@import '../untils/variables';

.list-answers {
  padding: 0;
  height: 110px;
  background: $list-answers-background;
  border-radius: 6px;
  overflow: auto;
  border: 1px solid $list-answers-lid-border;

  &--lid {
    border-right: none;
    border-bottom: none;
    border-left: none;
  }

  &--reservation {

  }

  &__item {
    display: flex;
    list-style: none;
    margin-bottom: 5px;
    gap: 5px;
    text-align: center;

    &--lid {
    }

    &--reservation {
      display: flex;
      align-items: center;
      justify-content: start;
      gap: 5px;
    }
  }

  &__button {
    border: none;
  }

  &__item-id {
    max-width: 50px;
    min-width: 50px;
  }

  &__item-date {
    max-width: 95px;
    min-width: 95px;
    text-align: center;
  }

  &__item-contact {
    max-width: 190px;
    min-width: 190px;
  }

  &__item-comment {
    max-width: 250px;
    min-width: 250px;
    text-align: start;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__item-open {
    max-width: 55px;
    min-width: 55px;
  }

  &__item-close {
    padding-top: 4px;
    color: #444444;
  }
}
