@import '../untils/variables';

.table {
  display: none;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: auto;
  font-size: 14px;
  line-height: 24px;
  border-collapse: collapse;
  text-align: center;
  overflow: hidden;

  &--show {
    display: table;
  }

  &__row {
    width: 100vh;
  }

  &__cell {
    padding: 5px;
    text-align: center;
    border: 1px solid $table-border-color;

    &--time {
      color: $table-time-color;
      background: $table-time-background-color;
      border-color: $table-time-border-color;
    }

    &--type {
      font-weight: 500;
      text-align: start;
      font-size: 17px;
      background: $table-date-background-color;
    }

    &--staff {
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: start;
      white-space: normal;

      &:hover {
        overflow: visible;
      }

    }
  }
}




