@import '../untils/variables';

.button {
  margin: 0;
  padding: 10px;
  color: $button-color;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  font-family: $general-font;
  border: 1px solid $button-border-color;
  border-radius: 6px;
  background: $button-background-color;
  text-transform: uppercase;
  text-decoration: none;
  outline: none;
  cursor: pointer;

  &:hover {
    color: $button-color-hover;
    background: $button-background-color-hover;
  }

  &:focus {
    border-color: $form-input-border-color-focus;
    box-shadow: 0 0 0 2px $form-input-outline-color-focus;
    outline: none;
  }

  &:active {
    opacity: 0.3;
  }

  &__icon {
    padding-right: 5px;
  }

  &--credit-management {
    margin-right: 10px;
  }

  &--mrRAuto {
    margin-right: auto;
  }

  &--close {
    padding: 2px 2px 0;
    background: none;
  }

  &--position {
    padding: 4px;
  }

  &--clear-booking-resources {
    border: none;
  }

  &--clear-positions {
    border: none;
  }
}

